import kList from '@/components/k-ui/k-user-list/k-list.vue'
export default {
	components: {
		kList
	},
	data() {
		return {
			curteamId: '',
			datas: ''
		};
	},
	onLoad(options) {
		this.curteamId = options.id
		this.loadData()
	},
	methods:{
		async loadData() {
			const res = await this.$api.request({
				url: '/team/applyList/' + this.curteamId
			})
			console.log(res)
			this.datas = res.data
		},
		userDetail(id){
			uni.navigateTo({
				url: '/pages/person/person-detail?id=' + id
			})
		},
	}
}
